import { Injectable } from '@angular/core';
import { Router } from '@angular/router';
import { Observable, forkJoin, of } from 'rxjs';
import {map} from 'rxjs/operators';
import { HttpClient } from "@angular/common/http";
import { environment } from "../../../environments/environment";
import { AppModule } from "../../../app/app.module";

@Injectable()
export class BaseService {

	_http : HttpClient;
	_api : string;
	_router: Router;
	constructor(api : string)
	{
		this._http = AppModule.injector.get(HttpClient);
		this._api = api;
		this._router = AppModule.injector.get(Router);
	}

	getById(id: any)
	{
		return this._http.get<any>(`${environment.serverUrl}${this._api}${id}`).pipe(map(e=>e));
	}

	get(params: string = '')
	{
		return this._http.get<Array<any>>(`${environment.serverUrl}${this._api}?${params}`).pipe(map(e=>e));
	}

	save(input: any): Observable<any> {

		if(input.id ==0)
			return this._http.post(`${environment.serverUrl}${this._api}`, input);
		else
			return this._http.put(`${environment.serverUrl}${this._api}${input.id}`, input);	
	}

	post(input: any): Observable<any> {
		return this._http.post(`${environment.serverUrl}${this._api}`, input);
	}

	delete(id: Number): Observable<any> {
		return this._http.delete(`${environment.serverUrl}${this._api}${id}`);
	} 
}
