import { BrowserModule, HAMMER_GESTURE_CONFIG } from '@angular/platform-browser';
import { NgModule,Injector, LOCALE_ID  } from '@angular/core'
import { registerLocaleData } from '@angular/common';
import localePt from '@angular/common/locales/pt';
import { NgSelectModule } from '@ng-select/ng-select';;
import { TranslateModule } from '@ngx-translate/core';
import { HttpClientModule,HTTP_INTERCEPTORS } from '@angular/common/http';

import { AppComponent } from './app.component';
import { AppRoutingModule } from './app-routing.module';
import { MatDialogModule, MAT_DATE_LOCALE, MatDividerModule, MAT_DATE_FORMATS, DateAdapter } from '@angular/material';

import { AuthenticationModule } from './core/auth/authentication.module';
import { NgxPermissionsModule } from 'ngx-permissions';

import { LayoutModule } from './content/layout/layout.module';
import { PartialsModule } from './content/partials/partials.module';
import { CoreModule } from './core/core.module';
import { AclService } from './core/services/acl.service';
import { LayoutConfigService } from './core/services/layout-config.service';
import { MenuConfigService } from './core/services/menu-config.service';
import { PageConfigService } from './core/services/page-config.service';
import { UserService } from './core/services/user.service';
import { UtilsService } from './core/services/utils.service';
import { ClassInitService } from './core/services/class-init.service';
import { NgbModule } from '@ng-bootstrap/ng-bootstrap';
import { BrowserAnimationsModule } from '@angular/platform-browser/animations';

import { 
	GestureConfig,
	MatProgressSpinnerModule,
	MatInputModule,
	MatSelectModule,
	MatSortModule,
	MatTableModule, 
} from '@angular/material';
import { OverlayModule } from '@angular/cdk/overlay';

import {CdkTableModule} from '@angular/cdk/table';


import { MessengerService } from './core/services/messenger.service';
import { ClipboardService } from './core/services/clipboard.sevice';

import { PERFECT_SCROLLBAR_CONFIG, PerfectScrollbarConfigInterface } from 'ngx-perfect-scrollbar';
import { LayoutConfigStorageService } from './core/services/layout-config-storage.service';
import { LogsService } from './core/services/logs.service';
import { QuickSearchService } from './core/services/quick-search.service';
import { SubheaderService } from './core/services/layout/subheader.service';
import { HeaderService } from './core/services/layout/header.service';
import { MenuHorizontalService } from './core/services/layout/menu-horizontal.service';
import { MenuAsideService } from './core/services/layout/menu-aside.service';
import { LayoutRefService } from './core/services/layout/layout-ref.service';
import { SplashScreenService } from './core/services/splash-screen.service';
import { DataTableService } from './core/services/datatable.service';
import { ClienteService } from './core/services/cliente.service';
import { UsuarioService } from './core/services/usuario.service';

import 'hammerjs';
import { ErrorDialogService } from './content/partials/layout/error-dialog/errordialog.service';
import { ErrorDialogComponent } from './content/partials/layout/error-dialog/errordialog.component';
import { HttpConfigInterceptor} from './core/interceptors/httpconfig.interceptor';
import { MOMENT_DATE_FORMATS, MomentDateAdapter } from './core/helpers/momentDateAdapter';
import { NgxLoadingModule, ngxLoadingAnimationTypes } from 'ngx-loading';
import { AlertService } from './core/services/alert.service';
import { LoaderService } from './core/services/loader.service';
import { LoaderInterceptor } from './core/interceptors/loader.interceptor';
import { UsuarioTipoService } from './core/services/usuarioTipo.service';
import { RelatorioService } from './core/services/relatorio.service';
import { DashboardService } from './core/services/dashboard.service';
import { PlanoService } from './core/services/plano.service';
import { PlanoTarifaService } from './core/services/planoTarifa.service';
import { MensagemProvedorService } from './core/services/mensagemProvedor.service';
const DEFAULT_PERFECT_SCROLLBAR_CONFIG: PerfectScrollbarConfigInterface = {
	suppressScrollX: true
};

registerLocaleData(localePt, 'pt-BR');

@NgModule({
	declarations: [AppComponent, ErrorDialogComponent],
	imports: [
		BrowserAnimationsModule,
		NgSelectModule,
		BrowserModule,
		AppRoutingModule,
		HttpClientModule,
		MatDialogModule,
		LayoutModule,
		PartialsModule,
		CoreModule,
		OverlayModule,
		AuthenticationModule,
		NgxPermissionsModule.forRoot(),
		NgbModule.forRoot(),
		TranslateModule.forRoot(),
		MatProgressSpinnerModule,
		CdkTableModule,
		BrowserModule,
		MatInputModule,
		MatSelectModule,
		MatTableModule,
		MatSortModule,
		MatProgressSpinnerModule,
		MatDividerModule,
		NgxLoadingModule.forRoot({
			animationType: ngxLoadingAnimationTypes.wanderingCubes,
			backdropBackgroundColour: 'rgba(0,0,0,0.1)', 
			backdropBorderRadius: '4px',
			primaryColour: '#ffffff', 
			secondaryColour: '#ffffff', 
			tertiaryColour: '#ffffff'
		})
	],
	providers: [
		AclService,
		LoaderService,
		LayoutConfigService,
		LayoutConfigStorageService,
		LayoutRefService,
		MenuConfigService,
		PageConfigService,
		UserService,
		ClienteService,
		UsuarioService,
		UsuarioTipoService,
		UtilsService,
		ClassInitService,
		MessengerService,
		ClipboardService,
		LogsService,
		QuickSearchService,
		DataTableService,
		SplashScreenService,
		ErrorDialogService,
		AlertService,
		LoaderService,
		RelatorioService,
		DashboardService,
		ClienteService,
		PlanoService,
		PlanoTarifaService,
		MensagemProvedorService,

		
		{ provide: HTTP_INTERCEPTORS, useClass: LoaderInterceptor, multi: true },
		{ provide: MAT_DATE_LOCALE, useValue: 'pt-BR'},
		{ provide: MAT_DATE_FORMATS, useValue: MOMENT_DATE_FORMATS },
		{ provide: DateAdapter, useClass: MomentDateAdapter },

		{ provide: LOCALE_ID, useValue: 'pt-BR' },
		{ provide: HTTP_INTERCEPTORS, useClass: HttpConfigInterceptor, multi: true },
		
		{
			provide: PERFECT_SCROLLBAR_CONFIG,
			useValue: DEFAULT_PERFECT_SCROLLBAR_CONFIG
		},

		// template services
		SubheaderService,
		HeaderService,
		MenuHorizontalService,
		MenuAsideService,
		{
			provide: HAMMER_GESTURE_CONFIG,
			useClass: GestureConfig
		}
	],
	entryComponents: [ErrorDialogComponent],
	bootstrap: [AppComponent]
})
export class AppModule {
	static injector: Injector;
	constructor(injector: Injector) {
        AppModule.injector = injector;
    }
}
