import { Injectable, EventEmitter } from '@angular/core';
import { HttpClient } from "@angular/common/http";
import { environment } from "../../../environments/environment";
import { Observable, forkJoin, of } from 'rxjs';
import { BaseService } from './base.service';
import { DashBoardFiltro } from '../models/dashboadFiltro';

@Injectable()
export class DashboardService extends BaseService {

	onAtualizar: EventEmitter<any> = new EventEmitter();
	constructor(){
		super('/api/dashboard/')
	}

	onPesquisar(filtro: DashBoardFiltro) {
        this.onAtualizar.emit(filtro);
    }


	public getResumo(model : DashBoardFiltro): Observable<any> {
		return this._http.post(`${environment.serverUrl}${this._api}resumo`, model);
	}

	public getScore(model : DashBoardFiltro): Observable<any> {
		return this._http.post(`${environment.serverUrl}${this._api}score`, model);
	}

	public getRegiao(model : DashBoardFiltro): Observable<any> {
		return this._http.post(`${environment.serverUrl}${this._api}regiao`, model);
	}
}
