import { Injectable, EventEmitter } from '@angular/core';
import { HttpClient } from "@angular/common/http";
import { environment } from "../../../environments/environment";
import { Observable, forkJoin, of } from 'rxjs';
import { BaseService } from './base.service';
import { RelatorioFiltro } from '../models/relatorio.filtro';
import { map } from 'rxjs/operators';

@Injectable()
export class RelatorioService extends BaseService {
	constructor(){
		super('/api/relatorio')
	}
	onAtualizar: EventEmitter<any> = new EventEmitter();

	onPesquisar(filtro: RelatorioFiltro) {
        this.onAtualizar.emit(filtro);
    }

	getFiltro(){
		return this._http.get<Array<RelatorioFiltro>>(`${environment.serverUrl}${this._api}/filtro`).pipe(map(e=>e));
  	}
  
	getRelatorioLoja(filtro : RelatorioFiltro){
		return this._http.post<Array<RelatorioFiltro>>(`${environment.serverUrl}${this._api}/loja`,filtro).pipe(map(e=>e));
	}

	getRelatorioCategoria(filtro : RelatorioFiltro){
		return this._http.post<Array<RelatorioFiltro>>(`${environment.serverUrl}${this._api}/categoria`,filtro).pipe(map(e=>e));
	}

	getRelatorioEmpresa(filtro : RelatorioFiltro){
		return this._http.post<Array<RelatorioFiltro>>(`${environment.serverUrl}${this._api}/empresa`,filtro).pipe(map(e=>e));
	}

	getRelatorioEmpresaDetalhe(filtro: RelatorioFiltro) {
	return this._http.post<Array<RelatorioFiltro>>(`${environment.serverUrl}${this._api}/empresa/categoria`,filtro).pipe(map(e=>e));
	}

	getRelatorioVendedor(filtro : RelatorioFiltro){
		return this._http.post<Array<RelatorioFiltro>>(`${environment.serverUrl}${this._api}/vendedor`,filtro).pipe(map(e=>e));
	}

	getRelatorioProduto(filtro : RelatorioFiltro){
		return this._http.post<Array<RelatorioFiltro>>(`${environment.serverUrl}${this._api}/produtomediapreco`,filtro).pipe(map(e=>e));
	}
}
